/* ==========================================================================
   Global Styles
   ========================================================================== */
/* Import a modern font inspired by Celona's clean typography */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background: linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
  color: #e6f0fa;
  line-height: 1.75;
}

.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* ==========================================================================
   Scroll Behavior
   ========================================================================== */
html {
  scroll-behavior: smooth;
}

main {
  scroll-snap-type: y mandatory;
  overflow-y: auto;
  height: 100vh;
}

.section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6rem 2rem;
  box-sizing: border-box;
  scroll-snap-align: start;
}

/* ==========================================================================
   Navigation
   ========================================================================== */
.navbar {
  position: sticky;
  top: 0;
  z-index: 50;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background: linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  animation: fadeIn 0.8s ease-out;
}

.logo-container {
  position: relative;
}

.logo {
  font-size: 2rem;
  font-weight: 700;
  color: #00e4ff;
  text-decoration: none;
  transition: transform 0.3s ease, color 0.3s ease, text-shadow 0.3s ease;
}

.logo:hover {
  transform: scale(1.05);
  color: #ffffff;
  text-shadow: 0 0 10px rgba(0, 228, 255, 0.5);
}

.header-symbol {
  position: absolute;
  top: 0;
  left: 0;
  width: 80px;
  height: 80px;
  background: url('D:\IWP\banyanz-website\public\footersymbol.png') no-repeat center center/cover;
  transition: transform 0.3s ease, filter 0.3s ease;
}

.header-symbol:hover {
  transform: scale(1.1);
  filter: drop-shadow(0 0 10px rgba(0, 228, 255, 0.5));
}

.nav-links {
  display: flex;
  gap: 2rem;
}

.nav-link {
  position: relative;
  color: #00e4ff;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

.nav-link:hover {
  color: #ffffff;
  text-shadow: 0 0 8px rgba(0, 228, 255, 0.4);
}

.nav-link::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -5px;
  left: 0;
  background: linear-gradient(to right, #00e4ff, #00aaff);
  transition: width 0.3s ease;
}

.nav-link:hover::after {
  width: 100%;
}

/* ==========================================================================
   Hero Section
   ========================================================================== */
.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 70vh;
  padding: 4rem 2rem;
  text-align: center;
  position: relative;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('D:\IWP\banyanz-website\public\background.jpg') no-repeat center center/cover, linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
  animation: slideUp 1s ease-out;
}

/* Add a subtle tech-inspired overlay */
.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(0, 228, 255, 0.1) 0%, transparent 70%);
  pointer-events: none;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 2rem;
}

/* ==========================================================================
   Sections
   ========================================================================== */
.home-section,
.products-services-section,
.industries-section,
.company-section,
.about-section,
.contact-section,
.features-section,
.community-section,
.join-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
  animation: slideUp 1s ease-out;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #00e4ff;
  margin-bottom: 1.5rem;
  text-align: center;
  text-shadow: 0 0 10px rgba(0, 228, 255, 0.3);
}

.section-text {
  font-size: 1.2rem;
  color: #c9d6e5;
  margin-bottom: 1.5rem;
  text-align: center;
}

.home-title {
  font-size: 3.5rem;
  font-weight: 700;
  color: #00e4ff;
  margin-bottom: 1.5rem;
  text-shadow: 0 0 15px rgba(0, 228, 255, 0.4);
}

.home-description {
  font-size: 1.3rem;
  color: #c9d6e5;
  margin-bottom: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

/* ==========================================================================
   Cards (Products, Industries, Features, Values)
   ========================================================================== */
.products-grid,
.industries-grid,
.features-grid,
.company-values {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.product-card,
.industry-card,
.feature-card,
.value-card {
  background: #15232b;
  padding: 2rem;
  border-radius: 12px;
  border: 1px solid transparent;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease, transform 0.3s ease, border 0.3s ease;
}

.product-card:hover,
.industry-card:hover,
.feature-card:hover,
.value-card:hover {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3);
  transform: translateY(-5px);
  border: 1px solid transparent;
  background: linear-gradient(145deg, #15232b, #1a2a32);
  box-shadow: 0 0 15px rgba(0, 228, 255, 0.3);
}

.product-title,
.industry-title,
.feature-title,
.value-title {
  font-size: 1.8rem;
  font-weight: 600;
  color: #00e4ff;
  margin-bottom: 1rem;
}

.product-text,
.industry-text,
.feature-text,
.value-text {
  font-size: 1.1rem;
  color: #c9d6e5;
  line-height: 1.6;
}

/* ==========================================================================
   Contact Section
   ========================================================================== */
.contact-box {
  background: #15232b;
  padding: 2rem;
  border-radius: 12px;
  max-width: 400px;
  margin: 0 auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease;
}

.contact-box:hover {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3), 0 0 15px rgba(0, 228, 255, 0.3);
}

.contact-info {
  font-size: 1.1rem;
  color: #c9d6e5;
  margin-bottom: 0.75rem;
}

.contact-link {
  color: #00e4ff;
  text-decoration: none;
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

.contact-link:hover {
  color: #ffffff;
  text-shadow: 0 0 8px rgba(0, 228, 255, 0.4);
}

/* ==========================================================================
   Learn More Sections
   ========================================================================== */
.learn-more-content {
  margin-top: 2rem;
  padding: 2rem;
  background: #15232b;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: left;
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.learn-more-content:hover {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3), 0 0 15px rgba(0, 228, 255, 0.3);
  transform: translateY(-5px);
}

.learn-more-text {
  font-size: 1.1rem;
  color: #c9d6e5;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.learn-more-subtitle {
  font-size: 1.5rem;
  font-weight: 600;
  color: #00e4ff;
  margin: 1.5rem 0 1rem;
}

.learn-more-list {
  list-style: disc;
  padding-left: 1.5rem;
  margin-bottom: 1.5rem;
}

.learn-more-list li {
  font-size: 1.1rem;
  color: #c9d6e5;
  margin-bottom: 0.75rem;
  line-height: 1.6;
}

/* ==========================================================================
   Modal Styles (Used in Industries.jsx)
   ========================================================================== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  overflow-y: auto;
  padding: 2rem;
}

.modal-content {
  background: #15232b;
  padding: 2rem;
  border-radius: 12px;
  max-width: 800px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
  animation: fadeIn 0.5s ease-out;
  border: 1px solid rgba(0, 228, 255, 0.2);
}

.modal-content:hover {
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5), 0 0 20px rgba(0, 228, 255, 0.4);
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 2rem;
  color: #00e4ff;
  cursor: pointer;
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

.modal-close:hover {
  color: #ffffff;
  text-shadow: 0 0 8px rgba(0, 228, 255, 0.4);
}

/* Ensure modal content aligns with existing styles */
.modal-content .industry-title {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.modal-content .section-text {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}

.modal-content .learn-more-subtitle {
  font-size: 1.5rem;
  margin: 1.5rem 0 1rem;
}

.modal-content .learn-more-text {
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

.modal-content .learn-more-list {
  margin-bottom: 1.5rem;
}

/* ==========================================================================
   Buttons
   ========================================================================== */
.btn-primary,
.btn-secondary {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  border-radius: 5px;
  text-decoration: none;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease, text-shadow 0.3s ease;
  animation: scaleIn 0.5s ease-out;
}

/* Gradient button inspired by Celona's vibrant style */
.btn-primary {
  background: linear-gradient(90deg, #00e4ff, #00aaff);
  color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.btn-primary:hover {
  background: linear-gradient(90deg, #00c4ff, #0088ff);
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3), 0 0 15px rgba(0, 228, 255, 0.5);
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.4);
}

.btn-secondary {
  background: transparent;
  border: 2px solid #00e4ff;
  color: #00e4ff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.btn-secondary:hover {
  background: linear-gradient(90deg, #00e4ff, #00aaff);
  color: #ffffff;
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3), 0 0 15px rgba(0, 228, 255, 0.5);
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.4);
}

/* ==========================================================================
   Footer
   ========================================================================== */
.footer {
  background: linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
  padding: 3rem 2rem;
  animation: fadeIn 1s ease-out;
}

.footer-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2.5rem;
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 2rem;
}

.footer-column {
  display: flex;
  flex-direction: column;
}

.footer-heading {
  font-size: 1.5rem;
  font-weight: 700;
  color: #00e4ff;
  margin-bottom: 1rem;
}

.footer-text {
  font-size: 1rem;
  color: #c9d6e5;
  line-height: 1.6;
}

.footer-link {
  color: #00e4ff;
  text-decoration: none;
  margin-bottom: 0.75rem;
  transition: color 0.3s ease, transform 0.3s ease, text-shadow 0.3s ease;
}

.footer-link:hover {
  color: #ffffff;
  transform: translateX(5px);
  text-shadow: 0 0 8px rgba(0, 228, 255, 0.4);
}

.footer-bottom {
  text-align: center;
  border-top: 1px solid rgba(0, 228, 255, 0.3);
  padding-top: 1.5rem;
}

/* ==========================================================================
   Animations
   ========================================================================== */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.95);
  }
  to {
    transform: scale(1);
  }
}

/* ==========================================================================
   Responsive Design
   ========================================================================== */
@media (max-width: 768px) {
  /* Navigation */
  .navbar {
    flex-direction: column;
    padding: 1rem;
  }

  .nav-links {
    flex-direction: column;
    gap: 1rem;
    margin-top: 1rem;
  }

  .header-symbol {
    top: 0.5rem;
    left: 1rem;
    width: 60px;
    height: 60px;
  }

  /* Hero Section */
  .hero {
    padding: 3rem 1rem;
    min-height: 60vh;
  }

  .hero-buttons {
    flex-direction: column;
    gap: 0.75rem;
  }

  .home-title {
    font-size: 2.5rem;
  }

  .home-description {
    font-size: 1.1rem;
  }

  /* Sections */
  .section {
    padding: 4rem 1rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .section-text {
    font-size: 1rem;
  }

  .product-title,
  .industry-title,
  .feature-title,
  .value-title {
    font-size: 1.5rem;
  }

  .product-text,
  .industry-text,
  .feature-text,
  .value-text {
    font-size: 0.9rem;
  }

  /* Learn More */
  .learn-more-content {
    padding: 1.5rem;
  }

  .learn-more-subtitle {
    font-size: 1.3rem;
  }

  .learn-more-text,
  .learn-more-list li {
    font-size: 0.9rem;
  }

  /* Modal */
  .modal-content {
    padding: 1.5rem;
    max-height: 70vh;
  }

  .modal-content .industry-title {
    font-size: 1.8rem;
  }

  .modal-content .section-text {
    font-size: 1rem;
  }

  .modal-content .learn-more-subtitle {
    font-size: 1.3rem;
  }

  .modal-content .learn-more-text {
    font-size: 0.9rem;
  }

  .modal-content .learn-more-list li {
    font-size: 0.9rem;
  }

  /* Footer */
  .footer {
    padding: 2rem 1rem;
  }

  .footer-container {
    gap: 1.5rem;
  }
}