/* ==========================================================================
   Global Styles
   ========================================================================== */
/* Import a modern font inspired by Celona's clean typography */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

html {
  scroll-behavior: smooth;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Inter', sans-serif;
  background: linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
  color: #e6f0fa;
}

/* ==========================================================================
   Header and Navigation
   ========================================================================== */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
  z-index: 50;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  animation: fadeIn 0.8s ease-out;
}

.nav {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem 2rem;
}

.nav-list {
  display: flex;
  justify-content: center;
  gap: 2rem;
  list-style: none;
  flex-wrap: wrap;
}

.nav-link {
  color: #00e4ff;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

.nav-link:hover {
  color: #ffffff;
  text-shadow: 0 0 8px rgba(0, 228, 255, 0.4);
}

/* ==========================================================================
   Main Content
   ========================================================================== */
main {
  scroll-snap-type: y mandatory;
  overflow-y: auto;
  height: 100vh;
}

.section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6rem 2rem;
  box-sizing: border-box;
  scroll-snap-align: start;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

/* Add a subtle tech-inspired overlay to sections */
.section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(0, 228, 255, 0.05) 0%, transparent 70%);
  pointer-events: none;
}

.home-section,
.about-section,
.company-section,
.contact-section,
.industries-section,
.join-section,
.products-services-section {
  width: 100%;
  max-width: 72rem;
  text-align: center;
  position: relative;
}

/* Home section has no background image, inherits from .app */
.home-section {
  background: none;
}

/* Cycle through the three background images for other sections */
.about-section {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('D:\IWP\banyanz-website\public\back.png'), linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
}

.company-section {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('D:\IWP\banyanz-website\public\back2.png'), linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
}

.contact-section {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('D:\IWP\banyanz-website\public\back3.png'), linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
}

.industries-section {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('D:\IWP\banyanz-website\public\back.png'), linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
}

.join-section {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('D:\IWP\banyanz-website\public\back2.png'), linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
}

.products-services-section {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('D:\IWP\banyanz-website\public\back3.png'), linear-gradient(to bottom right, #0a1a22, #1a2e38, #263e4e);
}

.home-title {
  font-size: 3rem;
  font-weight: 700;
  color: #00e4ff;
  margin-bottom: 1.5rem;
  text-shadow: 0 0 15px rgba(0, 228, 255, 0.4);
  animation: slideUp 1s ease-out;
}

.home-description {
  font-size: 1.25rem;
  color: #c9d6e5;
  margin-bottom: 2rem;
  animation: slideUp 1s ease-out 0.2s;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #00e4ff;
  margin-bottom: 1.5rem;
  text-shadow: 0 0 10px rgba(0, 228, 255, 0.3);
  animation: slideUp 1s ease-out;
}

.section-text {
  font-size: 1.125rem;
  color: #c9d6e5;
  margin-bottom: 1.5rem;
  max-width: 48rem;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
  animation: slideUp 1s ease-out 0.2s;
}

/* ==========================================================================
   Hero Buttons
   ========================================================================== */
.hero-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  margin-top: 1.5rem;
}

.btn-primary,
.btn-secondary {
  padding: 0.75rem 1.5rem;
  border-radius: 0.375rem;
  font-weight: 600;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease, text-shadow 0.3s ease;
  animation: scaleIn 0.5s ease-out;
}

.btn-primary {
  background: linear-gradient(90deg, #00e4ff, #00aaff);
  color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.btn-primary:hover {
  background: linear-gradient(90deg, #00c4ff, #0088ff);
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3), 0 0 15px rgba(0, 228, 255, 0.5);
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.4);
}

.btn-secondary {
  background: transparent;
  border: 2px solid #00e4ff;
  color: #00e4ff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.btn-secondary:hover {
  background: linear-gradient(90deg, #00e4ff, #00aaff);
  color: #ffffff;
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3), 0 0 15px rgba(0, 228, 255, 0.5);
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.4);
}

/* ==========================================================================
   Grids (Products, Industries, Values)
   ========================================================================== */
.products-grid,
.industries-grid,
.company-values {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 72rem;
  margin: 2rem auto;
}

.product-card,
.industry-card,
.value-card {
  background: #15232b;
  padding: 1.5rem;
  border-radius: 0.5rem;
  text-align: center;
  border: 1px solid transparent;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease, transform 0.3s ease, border 0.3s ease;
}

.product-card:hover,
.industry-card:hover,
.value-card:hover {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3);
  transform: translateY(-5px);
  background: linear-gradient(145deg, #15232b, #1a2a32);
  box-shadow: 0 0 15px rgba(0, 228, 255, 0.3);
}

.product-title,
.industry-title,
.value-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #00e4ff;
  margin-bottom: 1rem;
}

.product-text,
.industry-text,
.value-text {
  font-size: 1rem;
  color: #c9d6e5;
  line-height: 1.6;
}

/* ==========================================================================
   Contact Section
   ========================================================================== */
.contact-box {
  margin-top: 2rem;
  background: #15232b;
  padding: 1.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease;
}

.contact-box:hover {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3), 0 0 15px rgba(0, 228, 255, 0.3);
}

.contact-info {
  font-size: 1.125rem;
  color: #c9d6e5;
  margin-bottom: 1rem;
}

.contact-link {
  color: #00e4ff;
  text-decoration: none;
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

.contact-link:hover {
  color: #ffffff;
  text-shadow: 0 0 8px rgba(0, 228, 255, 0.4);
}

/* ==========================================================================
   Learn More Sections
   ========================================================================== */
.learn-more-content {
  margin-top: 2rem;
  padding: 1.5rem;
  background: #15232b;
  border-radius: 0.5rem;
  text-align: left;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.learn-more-content:hover {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3), 0 0 15px rgba(0, 228, 255, 0.3);
  transform: translateY(-5px);
}

.learn-more-text {
  font-size: 1rem;
  color: #c9d6e5;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.learn-more-subtitle {
  font-size: 1.25rem;
  font-weight: 600;
  color: #00e4ff;
  margin: 1rem 0;
}

.learn-more-list {
  list-style: disc;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
}

.learn-more-list li {
  font-size: 1rem;
  color: #c9d6e5;
  margin-bottom: 0.5rem;
  line-height: 1.6;
}

/* ==========================================================================
   Modal Styles (Used in Industries.jsx)
   ========================================================================== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  overflow-y: auto;
  padding: 2rem;
}

.modal-content {
  background: #15232b;
  padding: 2rem;
  border-radius: 0.5rem;
  max-width: 800px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
  animation: fadeIn 0.5s ease-out;
  border: 1px solid rgba(0, 228, 255, 0.2);
}

.modal-content:hover {
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5), 0 0 20px rgba(0, 228, 255, 0.4);
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 2rem;
  color: #00e4ff;
  cursor: pointer;
  transition: color 0.3s ease, text-shadow 0.3s ease;
}

.modal-close:hover {
  color: #ffffff;
  text-shadow: 0 0 8px rgba(0, 228, 255, 0.4);
}

/* Ensure modal content aligns with existing styles */
.modal-content .industry-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.modal-content .section-text {
  font-size: 1.125rem;
  margin-bottom: 1.5rem;
}

.modal-content .learn-more-subtitle {
  font-size: 1.25rem;
  margin: 1rem 0;
}

.modal-content .learn-more-text {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.modal-content .learn-more-list {
  margin-bottom: 1rem;
}

/* ==========================================================================
   Animations
   ========================================================================== */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.95);
  }
  to {
    transform: scale(1);
  }
}

/* ==========================================================================
   Responsive Design
   ========================================================================== */
@media (max-width: 768px) {
  .nav {
    padding: 1rem;
  }

  .nav-list {
    gap: 1rem;
  }

  .nav-link {
    font-size: 0.9rem;
  }

  .section {
    padding: 4rem 1rem;
  }

  .home-title {
    font-size: 2.5rem;
  }

  .home-description {
    font-size: 1.1rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .section-text {
    font-size: 1rem;
  }

  .hero-buttons {
    flex-direction: column;
    gap: 0.75rem;
  }

  .products-grid,
  .industries-grid,
  .company-values {
    gap: 1.5rem;
  }

  .product-card,
  .industry-card,
  .value-card {
    padding: 1rem;
  }

  .product-title,
  .industry-title,
  .value-title {
    font-size: 1.25rem;
  }

  .product-text,
  .industry-text,
  .value-text {
    font-size: 0.9rem;
  }

  .contact-info {
    font-size: 1rem;
  }

  .learn-more-content {
    padding: 1rem;
  }

  .learn-more-subtitle {
    font-size: 1.1rem;
  }

  .learn-more-text,
  .learn-more-list li {
    font-size: 0.9rem;
  }

  .modal-content {
    padding: 1.5rem;
    max-height: 70vh;
  }

  .modal-content .industry-title {
    font-size: 1.25rem;
  }

  .modal-content .section-text {
    font-size: 1rem;
  }

  .modal-content .learn-more-subtitle {
    font-size: 1.1rem;
  }

  .modal-content .learn-more-text {
    font-size: 0.9rem;
  }

  .modal-content .learn-more-list li {
    font-size: 0.9rem;
  }
}